import React, { Component, useEffect, useRef, useState } from "react";
import MetaTags from "react-meta-tags";
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  Col,
  Container,
  Label,
  Row,
} from "reactstrap";
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { fetchHelper } from "../../helpers/apiHelper";
import { salesURL } from "../../helpers/urlHelpers";

import moment from "moment";

import ReactToPrint from "react-to-print";
import { render } from "react-dom";
import { Link } from "react-router-dom";

const SalesDetails = (props) => {
  const [sales, setSales] = useState({});
  const [salesDetails, setSalesDetails] = useState([]);
  const totalRows = 19;

  const a5PrintRef = useRef();
  const a4PrintRef = useRef();

  useEffect(() => {
    fetchSaleInvoice();
  }, []);

  const fetchSaleInvoice = async () => {
    let salesId = props.location.state;
    const options = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ salesId }),
    };

    let data = await fetchHelper(`${salesURL}/getSalesDetails`, options);
    if (data.status) {
      setSales(data.data.sales);
      let temp = [];
      for (let i = 0; i < totalRows; ++i) {
        if (i < data.data.salesDetails.length) {
          let detail = data.data.salesDetails[i];
          temp.push({
            srNo: i + 1,
            product: detail.product,
            qty: detail.qty,
            rate: detail.rate,
            amt: detail.amt,
          });
        } else {
          temp.push({
            // srNo: undefined,
            // product: undefined,
            // qty: undefined,
            // rate: undefined,
            // amt: undefined,
          });
        }
        setSalesDetails([...temp]);
      }
    } else {
      alert(data.message);
    }
  };

  return (
    <React.Fragment>
      <div className="page-content" style={{ color: "black" }}>
        <MetaTags>
          <title>Dashboard | Sales Details</title>
        </MetaTags>
        <Container fluid={true}>
          {/* <Breadcrumbs title="Dashboard" breadcrumbItem="sales-details" /> */}

          <Row>
            <Col lg={12}>
              <Card className="m-0">
                <CardBody>
                  <Row>
                    <Col xs={4}>
                      <h5>Sales Details</h5>
                    </Col>

                    <Col xs={8}>
                      <Row>
                        <Col md={11} className="mb-1">
                          <div className="d-flex gap-4 justify-content-end">
                            <Label className="btn btn-sm btn-info mr-3">
                              <ReactToPrint
                                trigger={() => <div>A5 Print</div>}
                                content={() => a5PrintRef.current}
                              />
                            </Label>
                            <Label className="btn btn-sm btn-info">
                              <ReactToPrint
                                trigger={() => <div>A4 Print</div>}
                                content={() => a4PrintRef.current}
                              />
                            </Label>
                          </div>
                        </Col>
                        <Col md={1} className="mb-1">
                          <Button
                            className="btn btn-sm btn-danger float-end"
                            onClick={() => {
                              props.history.push("/sales");
                            }}
                          >
                            Back
                          </Button>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  {/* </CardTitle> */}
                  <SaleDetailComponentA5
                    ref={a5PrintRef}
                    sales={sales}
                    salesDetails={salesDetails}
                  />
                  <div hidden>
                    <SaleDetailComponentA4
                      ref={a4PrintRef}
                      sales={sales}
                      salesDetails={salesDetails}
                    />
                  </div>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  );
};

