import React, { useState, useEffect } from "react";
import { withRouter } from "react-router";
import MetaTags from "react-meta-tags";
import {
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Button,
  Modal,
  ModalBody,
  Input,
  Form,
  ModalHeader,
} from "reactstrap";

import { Edit, Trash2, BookOpen } from "react-feather";

// datatable related plugins
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory, {
  PaginationProvider,
  PaginationListStandalone,
  SizePerPageDropdownStandalone,
} from "react-bootstrap-table2-paginator";

import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import "./datatables.scss";

import { fetchHelper } from "../../helpers/apiHelper";
import { productURL } from "../../helpers/urlHelpers";
import { Link } from "react-router-dom";

function Product(props) {
  const [product, setProduct] = useState({});
  const [products, setProducts] = useState([]);
  const [page, setPage] = useState(1);
  const [sizePerPage, setSizePerPage] = useState(10);
  const [modalOpen, setOpenModal] = useState(false);
  const [form, setForm] = useState();
  const [updateForm, setUpdateForm] = useState();
  const [errMsg, setErrMsg] = useState(null);
  const [isEdit, setIsEdit] = useState(false);

  useEffect(async () => {
    await fetchProducts();
  }, []);

  const fetchProducts = async () => {
    const headers = {
      "Content-Type": "application/json",
    };

    let data = await fetchHelper(productURL, { headers });
    if (data.status) {
      setProducts(data.data);
    } else {
      alert(data.message);
    }
  };

  const handleDeleteProduct = async (productId) => {
    let headers = {
      "Content-Type": "application/json",
    };

    let body = JSON.stringify({
      productId,
    });

    let options = {
      method: "DELETE",
      headers,
      body,
    };

    let data = await fetchHelper(`${productURL}`, options);
    if (data.status) {
      await fetchProducts();
    } else {
    }
    alert(data.message);
  };

  const handleInputChange = (evt) => {
    setForm({ ...form, [evt.target.name]: evt.target.value });
  };

  const handleFormSubmit = async (evt) => {
    evt.preventDefault();
    if (errMsg) return;

    let body = JSON.stringify(form);
    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "POST",
      headers,
      body,
    };
    let data = await fetchHelper(productURL, options);
    alert(data.message);
    if (data.status) {
      setOpenModal(false);
      await fetchProducts();
    } else {
      setOpenModal(true);
    }
  };

  const handleUpdateInputChange = (evt) => {
    setUpdateForm({ ...updateForm, [evt.target.name]: evt.target.value });
  };

  const handleUpdateProduct = async (evt) => {
    evt.preventDefault();
    if (errMsg) return;

    let body = JSON.stringify(updateForm);
    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "PUT",
      headers,
      body,
    };
    let data = await fetchHelper(`${productURL}/${product._id}`, options);
    if (data.status) {
      setIsEdit(false);
      setUpdateForm({});
      setProduct({});
      await fetchProducts();
    } else {
      setIsEdit(true);
      alert(data.message);
    }
  };

  const columns = [
    {
      dataField: "name",
      text: "Name",
      sort: true,
    },
    {
      dataField: "rate",
      text: "Rate",
      sort: true,
    },
    {
      dataField: "details",
      isDummyField: true,
      text: "Details",
      sort: false,
      formatter: (cellContent, row) => (
        <div>
          <Link
            to={{ pathname: "/productDetails", state: row }}
            className="btn btn-sm btn-info"
          >
            Show
          </Link>
        </div>
      ),
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      formatter: (cellContent, row) => (
        <div className="d-flex gap-3 text-center">
          <Edit
            size={20}
            onClick={() => {
              setIsEdit(true);
              setProduct(row);
            }}
          />
          <Trash2
            size={20}
            onClick={async () => await handleDeleteProduct(row._id)}
          />
        </div>
      ),
    },
  ];

  const defaultSorted = [
    {
      dataField: "name",
      order: "asc",
    },
  ];

  // Custom Pagination Toggle
  const sizePerPageList = [
    { text: "5", value: 5 },
    { text: "10", value: 10 },
    { text: "15", value: 15 },
    { text: "20", value: 20 },
    { text: "25", value: 25 },
    { text: "All", value: products.length },
  ];

  const pageOptions = {
    sizePerPage,
    totalSize: products.length,
    sizePerPageList,
    custom: true,
  };

  const { SearchBar } = Search;

  return (
    <React.Fragment>
      <div className="page-content">
        <MetaTags>
          <title>Dashboard | Product</title>
        </MetaTags>
        <div className="container-fluid">
          <Breadcrumbs title="Dashboard" breadcrumbItem="product" />
          <Row>
            <Col className="col-12">
              <Card>
                <CardBody>
                  <PaginationProvider
                    pagination={paginationFactory(pageOptions)}
                    keyField="_id"
                    columns={columns}
                    data={products}
                  >
                    {({ paginationProps, paginationTableProps }) => (
                      <ToolkitProvider
                        keyField="_id"
                        columns={columns}
                        data={products}
                        search
                      >
                        {(toolkitProps) => (
                          <React.Fragment>
                            {/* Search Bar & Add Product Button */}
                            <Row className="mb-2">
                              <Col sm="6" xs="12">
                                <div className="search-box mb-2 d-inline-block">
                                  <div className="position-relative">
                                    <SearchBar {...toolkitProps.searchProps} />
                                    <i className="bx bx-search-alt search-icon" />
                                  </div>
                                </div>
                              </Col>
                              <Col sm="6" xs="12">
                                <div>
                                  <Button
                                    className="btn btn-success btn-md float-end"
                                    onClick={() => setOpenModal(true)}
                                  >
                                    Add Product
                                  </Button>
                                </div>
                              </Col>
                            </Row>

                            {/* Table Row */}
                            <Row>
                              <Col xl="12">
                                <div className="table-responsive">
                                  <BootstrapTable
                                    keyField={"_id"}
                                    responsive
                                    bordered={false}
                                    striped={false}
                                    defaultSorted={defaultSorted}
                                    classes={"table align-middle table-nowrap"}
                                    headerWrapperClasses={"thead-light"}
                                    {...toolkitProps.baseProps}
                                    {...paginationTableProps}
                                  />
                                </div>
                              </Col>
                            </Row>

                            {/* Pagination Row, i.e., Size per page option & Page number option */}
                            <Row className="align-items-md-center mt-30">
                              <Col className="inner-custom-pagination d-flex">
                                <div className="d-inline">
                                  <SizePerPageDropdownStandalone
                                    {...paginationProps}
                                  />
                                </div>
                                <div className="text-md-right ms-auto">
                                  <PaginationListStandalone
                                    {...paginationProps}
                                  />
                                </div>
                              </Col>
                            </Row>
                          </React.Fragment>
                        )}
                      </ToolkitProvider>
                    )}
                  </PaginationProvider>

                  {/* Modal For Add Product */}
                  <Row>
                    <Modal size="xl" isOpen={modalOpen}>
                      <ModalBody>
                        <ModalHeader
                          toggle={() => {
                            setOpenModal(!modalOpen);
                            setErrMsg(null);
                          }}
                          tag="h4"
                          className="mb-0"
                        >
                          {"Add Product"}
                        </ModalHeader>
                        <Card>
                          <CardBody>
                            <Form
                              onSubmit={handleFormSubmit}
                              autoComplete="off"
                            >
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="name">
                                    Name <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="Name"
                                    name="name"
                                    required
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="rate">
                                    Rate <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="number"
                                    placeholder="Rate"
                                    name="rate"
                                    min="0.00"
                                    required
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row className="mt-2 mb-0">
                                <Col md="12">
                                  <Button
                                    type="submit"
                                    className="btn btn-success btn-md float-end"
                                  >
                                    Save
                                  </Button>
                                </Col>
                              </Row>
                            </Form>
                          </CardBody>
                        </Card>
                      </ModalBody>
                    </Modal>
                  </Row>

                  {/* Modal For Edit Product */}
                  <Row>
                    <Modal size="xl" isOpen={isEdit}>
                      <ModalBody>
                        <ModalHeader
                          toggle={() => {
                            setIsEdit(!isEdit);
                            setErrMsg(null);
                          }}
                          tag="h4"
                          className="mb-0"
                        >
                          {"Edit Product"}
                        </ModalHeader>
                        <Card>
                          <CardBody>
                            <Form
                              onSubmit={handleUpdateProduct}
                              autoComplete="off"
                            >
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="name">
                                    Name <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="Name"
                                    name="name"
                                    required
                                    defaultValue={product.name}
                                    onChange={handleUpdateInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="rate">
                                    Rate <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="number"
                                    placeholder="Rate"
                                    name="rate"
                                    min="0.00"
                                    required
                                    defaultValue={product.rate}
                                    onChange={handleUpdateInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row className="mt-2 mb-0">
                                <Col md="12">
                                  <Button
                                    type="submit"
                                    className="btn btn-info btn-md float-end"
                                  >
                                    Update
                                  </Button>
                                </Col>
                              </Row>
                            </Form>
                          </CardBody>
                        </Card>
                      </ModalBody>
                    </Modal>
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </React.Fragment>
  );
}

export default withRouter(Product);
