import React, { useState, useEffect } from "react";
import { withRouter } from "react-router";
import MetaTags from "react-meta-tags";
import { Row, Col, Card, CardBody, Button, Input, Form } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { fetchHelper } from "../../helpers/apiHelper";
import { profileURL } from "../../helpers/urlHelpers";
import { StateList } from "../../common/StateList";

function Product(props) {
  const [form, setForm] = useState(new FormData());

  useEffect(async () => {
    await fetchProfileDetails();
  }, []);

  const fetchProfileDetails = async () => {
    const headers = {
      "Content-Type": "application/json",
    };

    let data = await fetchHelper(profileURL + "/userDetails", { headers });
    if (data.status) {
      setForm(data.data);
    } else {
      alert(data.message);
    }
  };

  const handleInputChange = (evt) => {
    setForm({ ...form, [evt.target.name]: evt.target.value });
  };

  const handleFormSubmit = async (evt) => {
    evt.preventDefault();

    let body = JSON.stringify({ ...form });
    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "POST",
      headers,
      body,
    };
    let data = await fetchHelper(profileURL + "/updateUser", options);
    alert(data.message);
    if (data.status) {
      props.history.push("/dashboard");
    } else {
      return;
    }
  };

  return (
    <React.Fragment>
      <div className="page-content">
        <MetaTags>
          <title>User | Profile</title>
        </MetaTags>
        <div className="container-fluid">
          <Breadcrumbs title="User" breadcrumbItem="profile" />
          <Row>
            <Col className="col-12">
              <Card>
                <CardBody>
                  <Form onSubmit={handleFormSubmit} autoComplete="off">
                    <Row>
                      <Col md="6" className="mb-2">
                        <label htmlFor="username">
                          Name <span style={{ color: "red" }}>*</span>
                        </label>
                        <Input
                          type="text"
                          placeholder="User Name"
                          name="username"
                          required
                          value={form.username}
                          onChange={handleInputChange}
                        />
                      </Col>
                      <Col md="6" className="mb-2">
                        <label htmlFor="firmName">
                          Firm Name <span style={{ color: "red" }}>*</span>
                        </label>
                        <Input
                          type="text"
                          placeholder="Firm Name"
                          name="firmName"
                          required
                          value={form.firmName}
                          onChange={handleInputChange}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col md="4" className="mb-2">
                        <label htmlFor="mobileNo">
                          Mobile No. <span style={{ color: "red" }}>*</span>
                        </label>
                        <Input
                          type="tel"
                          placeholder="XXXXXXXXXX"
                          name="mobileNo"
                          pattern="[0-9]{10}"
                          required
                          value={form.mobileNo}
                          onChange={handleInputChange}
                        />
                      </Col>
                      <Col md="4" className="mb-2">
                        <label htmlFor="gstNo">
                          GST No. <span style={{ color: "red" }}>*</span>
                        </label>
                        <Input
                          type="text"
                          placeholder="GST No."
                          name="gstNo"
                          required
                          value={form.gstNo}
                          onChange={handleInputChange}
                        />
                      </Col>
                      <Col md="4" className="mb-2">
                        <label htmlFor="panNo">
                          PAN No. <span style={{ color: "red" }}>*</span>
                        </label>
                        <Input
                          type="text"
                          placeholder="PAN No."
                          name="panNo"
                          required
                          value={form.panNo}
                          onChange={handleInputChange}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col md="6" className="mb-2">
                        <label htmlFor="address">Address</label>
                        <Input
                          type="text"
                          placeholder="Address"
                          name="address"
                          value={form.address}
                          onChange={handleInputChange}
                        />
                      </Col>
                      <Col md="6" className="mb-2">
                        <label htmlFor="state">State</label>
                        <Input
                          type="select"
                          name="state"
                          value={form.state}
                          onChange={handleInputChange}
                        >
                          <option value="0">Select State</option>
                          {StateList.map(({ state }, index) => (
                            <option value={state} key={index}>
                              {state}
                            </option>
                          ))}
                        </Input>
                      </Col>
                    </Row>
                    <Row className="mt-2 mb-0">
                      <Col md="12" className="text-center mt-3">
                        <Button type="submit" className="btn btn-info btn-md">
                          Save
                        </Button>
                      </Col>
                    </Row>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </React.Fragment>
  );
}

export default withRouter(Product);
