import React from "react";
import { Redirect } from "react-router-dom";

import Login from "../pages/Authentication/Login";
import Logout from "../pages/Authentication/Logout";

import Register from "../pages/Authentication/Register";

import Dashboard from "../pages/Dashboard/index";

// Profile Route
import Profile from "../pages/Profile/index";

// Party Routes
import Party from "../pages/Party/index";
import PartyDetails from "../pages/Party/PartyDetails";

// Product Routes
import Product from "../pages/Product/index";
import ProductDetails from "../pages/Product/ProductDetails";

// Sales Routes
import Sales from "../pages/Sales/index";
import SalesAdd from "../pages/Sales/addSales";
import SalesDetails from "../pages/Sales/salesDetails";

// Reports Routes
import Reports from "../pages/Reports";

const authProtectedRoutes = [
  { path: "/dashboard", component: Dashboard },

  // Profile Routes
  { path: "/profile", component: Profile },

  // Party Routes
  { path: "/party", component: Party },
  { path: "/partyDetails", component: PartyDetails },

  // // Product Routes
  // { path: "/product", component: Product },
  // { path: "/productDetails", component: ProductDetails },

  // Sales Routes
  { path: "/sales", component: Sales },
  { path: "/salesAdd", component: SalesAdd },
  { path: "/salesDetails", component: SalesDetails },

  // Reports Routes
  { path: "/report", component: Reports },

  // this route should be at the end of all other routes
  { path: "/", exact: true, component: () => <Redirect to="/dashboard" /> },
];

const publicRoutes = [
  { path: "/login", component: Login },
  { path: "/register", component: Register },
  { path: "/logout", component: Logout },
];

export { authProtectedRoutes, publicRoutes };
