import React, { useState, useEffect } from "react";
import { withRouter } from "react-router";
import MetaTags from "react-meta-tags";
import { Row, Col } from "reactstrap";

// datatable related plugins
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory, {
  PaginationProvider,
  PaginationListStandalone,
  SizePerPageDropdownStandalone,
} from "react-bootstrap-table2-paginator";

import ToolkitProvider from "react-bootstrap-table2-toolkit";

import { fetchHelper } from "../../../helpers/apiHelper";
import { reportURL } from "../../../helpers/urlHelpers";

import { productReportColumns } from "./tableColumns";
import { ExportToExcel } from "../../../helpers/ExportToExcel";

function ProductReport({ party }) {
  const [productList, setProductList] = useState([]);
  const [excelList, setExcelList] = useState([]);
  const [sizePerPage, setSizePerPage] = useState(10);

  useEffect(async () => {
    await fetchProductReportList();
  }, [party]);

  const fetchProductReportList = async () => {
    const headers = {
      "Content-Type": "application/json",
    };

    const body = JSON.stringify({ party });

    let data = await fetchHelper(`${reportURL}/product`, {
      headers,
      body,
      method: "POST",
    });

    if (data.status) {
      setProductList(data.data);
      setExcelList([
        ...data.data.map((product) => ({
          "Sr. No.": product.srNo,
          Party: product.partyName,
          "Product Name": product.product,
          Rate: product.rate,
        })),
        {},
        {
          "Sr. No.": `Total : ${data.data.length}`,
          Party: "",
          "Product Name": "",
          Rate: "",
        },
      ]);
    } else {
      alert(data.message);
    }
  };

  const defaultSorted = [
    {
      dataField: "srNo",
      order: "asc",
    },
  ];

  const sizePerPageList = [
    { text: "10", value: 10 },
    { text: "15", value: 15 },
    { text: "20", value: 20 },
    { text: "All", value: productList.length },
  ];

  const pageOptions = {
    sizePerPage,
    totalSize: productList.length,
    sizePerPageList,
    custom: true,
  };

  return (
    <React.Fragment>
      <MetaTags>
        <title>Dashboard | Product Report</title>
      </MetaTags>
      <div className="container-fluid">
        <PaginationProvider
          pagination={paginationFactory(pageOptions)}
          keyField="_id"
          columns={productReportColumns}
          data={productList}
        >
          {({ paginationProps, paginationTableProps }) => (
            <ToolkitProvider
              keyField="_id"
              columns={productReportColumns}
              data={productList}
              search
            >
              {(toolkitProps) => (
                <React.Fragment>
                  {/* Export Excel Button */}
                  <Row>
                    <Col xs="12" className="float-end">
                      <ExportToExcel
                        data={excelList}
                        fileName="Product Report"
                        btnText="Export To Excel"
                      />
                    </Col>
                  </Row>

                  {/* Table Row */}
                  <Row>
                    <Col xl="12">
                      <div className="table-responsive">
                        <BootstrapTable
                          keyField={"_id"}
                          responsive
                          bordered={false}
                          striped={false}
                          defaultSorted={defaultSorted}
                          classes={"table align-middle table-nowrap"}
                          headerWrapperClasses={"thead-light"}
                          {...toolkitProps.baseProps}
                          {...paginationTableProps}
                        />
                      </div>
                    </Col>
                  </Row>

                  {/* Pagination Row, i.e., Size per page option & Page number option */}
                  <Row className="align-items-md-center mt-3">
                    <Col className="inner-custom-pagination d-flex">
                      <div className="d-inline">
                        <SizePerPageDropdownStandalone {...paginationProps} />
                      </div>
                      <div className="text-md-right ms-auto">
                        <PaginationListStandalone {...paginationProps} />
                      </div>
                    </Col>
                  </Row>
                </React.Fragment>
              )}
            </ToolkitProvider>
          )}
        </PaginationProvider>
      </div>
    </React.Fragment>
  );
}

export default withRouter(ProductReport);
