import React, { useState, useEffect } from "react";
import { withRouter } from "react-router";
import MetaTags from "react-meta-tags";
import { Row, Col, Card, CardBody, Input } from "reactstrap";

import { Trash2, BookOpen } from "react-feather";

// datatable related plugins
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory, {
  PaginationProvider,
  PaginationListStandalone,
  SizePerPageDropdownStandalone,
} from "react-bootstrap-table2-paginator";

import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import "./datatables.scss";

import { fetchHelper } from "../../helpers/apiHelper";
import { salesURL } from "../../helpers/urlHelpers";
import { Link } from "react-router-dom";

import { ExportToExcel } from "../../helpers/ExportToExcel";

import moment from "moment";

import { subDays, addDays, subMonths } from "date-fns";

import { DateRangePicker } from "rsuite";
import "rsuite/dist/rsuite.min.css";
import ExpandSalesRow from "./ExpandSalesRow";

function Sales(props) {
  const [sales, setSales] = useState([]);
  const [excelList, setExcelList] = useState([]);
  const [sizePerPage, setSizePerPage] = useState(10);
  const [string, setString] = useState("");

  const [duration, setDuration] = useState({
    startDate: subMonths(new Date(), 1),
    endDate: new Date(),
  });

  const calendarRanges = [
    {
      label: "Yesterday",
      value: [subDays(new Date(), 1), subDays(new Date(), 1)],
    },
    {
      label: "Today",
      value: [
        new Date(new Date().setHours(0, 0, 0, 0)),
        new Date(new Date().setHours(23, 59, 59, 999)),
      ],
    },
    {
      label: "Tomorrow",
      value: [addDays(new Date(), 1), addDays(new Date(), 1)],
    },
    {
      label: "Last 7 Days",
      value: [subDays(new Date(), 6), new Date()],
    },
    {
      label: "Last Month",
      value: [subDays(new Date(), 29), new Date()],
    },
  ];

  // const formatDate = (date) => moment(date).format("DD/MM/yyyy HH:mm:ss A");
  const formatDate = (date) => moment(date).format("DD/MM/yyyy");

  useEffect(async () => {
    await fetchSales();
  }, [string, duration]);

  const fetchSales = async () => {
    const headers = {
      "Content-Type": "application/json",
    };

    const body = JSON.stringify({ string, duration });

    let data = await fetchHelper(`${salesURL}/getAllSales`, {
      headers,
      body,
      method: "POST",
    });
    if (data.status) {
      let sales = data.data;
      setSales(sales);
      setExcelList([
        ...sales.map((sale, index) => ({
          "Sr. No.": index + 1,
          Party: sale.party.partyName,
          "Sales No.": sale.salesNo,
          Date: moment(sale.salesDate).format("DD MMM, YYYY"),
          "Qty.": sale.totalQty,
          Amount: sale.totalAmt,
          Discount: `${sale.disAmt} (${sale.disPer}%)`,
          GST: `${
            parseFloat(sale.cgstAmt || 0) +
            parseFloat(sale.sgstAmt || 0) +
            parseFloat(sale.igstAmt || 0)
          } (${
            parseFloat(sale.cgstPer || 0) +
            parseFloat(sale.sgstPer || 0) +
            parseFloat(sale.igstPer || 0)
          }%)`,
          "Net Amount": sale.netAmt,
          "Round Amount": sale.roundAmt,
          "Challan No.": sale.challanNo || "",
          Remarks: sale.remarks || "",
        })),
        {},
        {
          "Sr. No.": `Total : ${sales.length}`,
          Party: "",
          "Sales No.": "",
          Date: "",
          "Qty.": "",
          Amount: "",
          Discount: "",
          GST: "",
          "Net Amount": sales.reduce(
            (prev, curr) => ({ netAmt: prev.netAmt + (curr?.netAmt || 0) }),
            { netAmt: 0 }
          ).netAmt,
          "Round Amount": sales.reduce(
            (prev, curr) => ({
              roundAmt: prev.roundAmt + (curr?.roundAmt || 0),
            }),
            { roundAmt: 0 }
          ).roundAmt,
          "Challan No.": "",
          Remarks: "",
        },
      ]);
    } else {
      alert(data.message);
    }
  };

  const handleDeleteSales = async (salesId) => {
    if (
      window.confirm(
        "Are you sure you want to delete this sales invoice ???"
      ) === false
    )
      return;

    let headers = {
      "Content-Type": "application/json",
    };

    let body = JSON.stringify({
      salesId,
    });

    let options = {
      method: "DELETE",
      headers,
      body,
    };

    let data = await fetchHelper(`${salesURL}/deleteSales`, options);
    if (data.status) {
      await fetchSales();
    } else {
    }
    alert(data.message);
  };

  const columns = [
    {
      dataField: "party.partyName",
      text: "Party",
      sort: true,
      footer: (columnData) => `Total : ${columnData.length}`,
    },
    {
      dataField: "salesNo",
      text: "Sales No.",
      sort: true,
      footer: (columnData) => "",
    },
    {
      dataField: "salesDate",
      text: "Date",
      sort: true,
      formatter: (cellContent, row) => formatDate(row.salesDate),
      footer: (columnData) => "",
    },
    {
      dataField: "totalQty",
      text: "Qty.",
      sort: false,
      footer: (columnData) => "",
    },
    {
      dataField: "totalAmt",
      text: "Amount",
      sort: false,
      footer: (columnData) => "",
    },
    {
      dataField: "discount",
      text: "Discount",
      sort: false,
      formatter: (cellContent, row) =>
        `${parseFloat(row.disAmt || 0).toFixed(2)} (${parseFloat(
          row.disPer || 0
        )}%)`,
      footer: (columnData) => "",
    },
    {
      dataField: "gst",
      text: "GST",
      sort: false,
      formatter: (cellContent, row) =>
        `${(
          parseFloat(row.cgstAmt || 0) +
          parseFloat(row.sgstAmt || 0) +
          parseFloat(row.igstAmt || 0)
        ).toFixed(2)} (${
          parseFloat(row.cgstPer || 0) +
          parseFloat(row.sgstPer || 0) +
          parseFloat(row.igstPer || 0)
        }%)`,
      footer: (columnData) => "",
    },
    {
      dataField: "netAmt",
      text: "Net Amount",
      sort: true,
      footer: (columnData) =>
        columnData.reduce((prev, curr) => prev + (curr || 0), 0).toFixed(2),
    },
    {
      dataField: "roundAmt",
      text: "Round Amount",
      sort: true,
      footer: (columnData) =>
        columnData.reduce((prev, curr) => prev + (curr || 0), 0).toFixed(2),
    },
    {
      dataField: "challanNo",
      text: "Challan No.",
      sort: true,
      footer: (columnData) => "",
    },
    {
      dataField: "remarks",
      text: "Remarks",
      sort: true,
      footer: (columnData) => "",
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      formatter: (cellContent, row) => (
        <div className="d-flex gap-3 text-center">
          <Link
            to={{ pathname: "/salesDetails", state: row._id }}
            className="text-success"
          >
            <BookOpen size={20} />
          </Link>
          {/* <Link
            to={{ pathname: "/salesEdit", state: row._id }}
            className="text-primary"
          >
            <Edit size={20} />
          </Link> */}
          <Trash2
            size={20}
            onClick={async () => await handleDeleteSales(row._id)}
            className="text-danger"
          />
        </div>
      ),
      footer: (columnData) => "",
    },
  ];

  const defaultSorted = [
    {
      dataField: "salesDate",
      order: "desc",
    },
  ];

  // Custom Pagination Toggle
  const sizePerPageList = [
    { text: "10", value: 10 },
    { text: "15", value: 15 },
    { text: "20", value: 20 },
    { text: "All", value: sales.length },
  ];

  const pageOptions = {
    sizePerPage,
    totalSize: sales.length,
    sizePerPageList,
    custom: true,
  };

  const { SearchBar } = Search;

  return (
    <React.Fragment>
      <div className="page-content">
        <MetaTags>
          <title>Dashboard | Sales</title>
        </MetaTags>
        <div className="container-fluid">
          <Breadcrumbs title="Dashboard" breadcrumbItem="sales" />
          <Row>
            <Col className="col-12">
              <Card>
                <CardBody>
                  <PaginationProvider
                    pagination={paginationFactory(pageOptions)}
                    keyField="_id"
                    columns={columns}
                    data={sales}
                  >
                    {({ paginationProps, paginationTableProps }) => (
                      <ToolkitProvider
                        keyField="_id"
                        columns={columns}
                        data={sales}
                        search
                      >
                        {(toolkitProps) => (
                          <React.Fragment>
                            {/* Search Bar & Add Product Button */}
                            <Row className="mb-2 row-main">
                              <Col md="4" sm="6" xs="12">
                                <div className="search-box mb-2 d-inline-block search-box-div">
                                  <div className="position-relative">
                                    <Input
                                      type="text"
                                      placeholder="Search . . ."
                                      onChange={(evt) => {
                                        setString(evt.target.value);
                                      }}
                                    />
                                    <i className="bx bx-search-alt search-icon" />
                                  </div>
                                </div>
                              </Col>
                              <Col md="4" sm="6" xs="12">
                                <DateRangePicker
                                  className="payment-detail-calendar mb-2 date-picker-div"
                                  onChange={(item) => {
                                    let [startDate, endDate] = item;
                                    setDuration({ startDate, endDate });
                                  }}
                                  showOneCalendar
                                  format="dd/MM/yyyy"
                                  defaultValue={[
                                    duration.startDate,
                                    duration.endDate,
                                  ]}
                                  ranges={calendarRanges}
                                />
                              </Col>
                              <Col md="4" sm="12" xs="12" className="btn-col">
                                <div className="d-flex float-end gap-2">
                                  <Link
                                    to={{ pathname: "/salesAdd" }}
                                    className="btn btn-info btn-md mr-1"
                                  >
                                    Add
                                  </Link>

                                  <ExportToExcel
                                    data={excelList}
                                    btnText="Excel"
                                    fileName="Sales Report"
                                  />
                                </div>
                              </Col>
                            </Row>

                            {/* Table Row */}
                            <Row>
                              <Col xl="12">
                                <div className="table-responsive">
                                  <BootstrapTable
                                    keyField={"_id"}
                                    responsive
                                    bordered={false}
                                    striped={false}
                                    defaultSorted={defaultSorted}
                                    classes={"table align-middle table-nowrap"}
                                    headerWrapperClasses={"thead-light"}
                                    {...toolkitProps.baseProps}
                                    {...paginationTableProps}
                                    /* expandRow={{
                                      renderer: (sales) => (
                                        <ExpandSalesRow sales={sales} />
                                      ),
                                    }} */
                                  />
                                </div>
                              </Col>
                            </Row>

                            {/* Pagination Row, i.e., Size per page option & Page number option */}
                            <Row className="align-items-md-center mt-3">
                              <Col className="inner-custom-pagination d-flex">
                                <div className="d-inline">
                                  <SizePerPageDropdownStandalone
                                    {...paginationProps}
                                  />
                                </div>
                                <div className="text-md-right ms-auto">
                                  <PaginationListStandalone
                                    {...paginationProps}
                                  />
                                </div>
                              </Col>
                            </Row>
                          </React.Fragment>
                        )}
                      </ToolkitProvider>
                    )}
                  </PaginationProvider>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </React.Fragment>
  );
}

export default withRouter(Sales);
