import React, { useState } from "react";
import {
  Button,
  Card,
  CardBody,
  Col,
  Container,
  Form,
  Input,
  Row,
} from "reactstrap";

import { Link } from "react-router-dom";
import { StateList } from "../../common/StateList";
import { fetchHelper } from "../../helpers/apiHelper";
import { profileURL } from "../../helpers/urlHelpers";

const Register = (props) => {
  const [form, setForm] = useState(new FormData());

  const handleInputChange = (evt) =>
    setForm({ ...form, [evt.target.name]: evt.target.value });

  const handleFormSubmit = async (evt) => {
    evt.preventDefault();

    if (form.password != form.confirmPassword) {
      return alert("Enter matching passwords");
    }

    delete form.confirmPassword;

    let body = JSON.stringify({ ...form });
    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "POST",
      headers,
      body,
    };
    let data = await fetchHelper(profileURL, options);
    console.log(data);
    alert(data.message);
    if (data.status) {
      props.history.push("/dashboard");
    } else {
      return;
    }
  };

  return (
    <React.Fragment>
      <div className="home-btn d-none d-sm-block">
        <Link to="/" className="text-dark">
          <i className="bx bx-home h2" />
        </Link>
      </div>
      <div className="account-pages my-5 pt-sm-5">
        <Container>
          <Row className="justify-content-center">
            <Col md={10} lg={9} xl={8}>
              <Row>
                <Col className="col-12">
                  <Card>
                    <CardBody>
                      <Form onSubmit={handleFormSubmit} autoComplete="off">
                        <Row>
                          <Col md="6" className="mb-2">
                            <label htmlFor="username">
                              Username <span style={{ color: "red" }}>*</span>
                            </label>
                            <Input
                              type="text"
                              placeholder="User Name"
                              name="username"
                              required
                              value={form.username}
                              onChange={handleInputChange}
                            />
                          </Col>
                          <Col md="6" className="mb-2">
                            <label htmlFor="firmName">
                              Firm Name <span style={{ color: "red" }}>*</span>
                            </label>
                            <Input
                              type="text"
                              placeholder="Firm Name"
                              name="firmName"
                              required
                              value={form.firmName}
                              onChange={handleInputChange}
                            />
                          </Col>
                          <Col md="6" className="mb-2">
                            <label htmlFor="password">
                              Password <span style={{ color: "red" }}>*</span>
                            </label>
                            <Input
                              type="password"
                              placeholder="Password"
                              name="password"
                              required
                              value={form.password}
                              onChange={handleInputChange}
                            />
                          </Col>
                          <Col md="6" className="mb-2">
                            <label htmlFor="confirmPassword">
                              Confirm Password{" "}
                              <span style={{ color: "red" }}>*</span>
                            </label>
                            <Input
                              type="password"
                              placeholder="Confirm Password"
                              name="confirmPassword"
                              required
                              value={form.confirmPassword}
                              onChange={handleInputChange}
                            />
                          </Col>
                        </Row>
                        <Row>
                          <Col md="4" className="mb-2">
                            <label htmlFor="mobileNo">
                              Mobile No. <span style={{ color: "red" }}>*</span>
                            </label>
                            <Input
                              type="tel"
                              placeholder="XXXXXXXXXX"
                              name="mobileNo"
                              pattern="[0-9]{10}"
                              required
                              value={form.mobileNo}
                              onChange={handleInputChange}
                            />
                          </Col>
                          <Col md="4" className="mb-2">
                            <label htmlFor="gstNo">
                              GST No. <span style={{ color: "red" }}>*</span>
                            </label>
                            <Input
                              type="text"
                              placeholder="GST No."
                              name="gstNo"
                              required
                              value={form.gstNo}
                              onChange={handleInputChange}
                            />
                          </Col>
                          <Col md="4" className="mb-2">
                            <label htmlFor="panNo">
                              PAN No. <span style={{ color: "red" }}>*</span>
                            </label>
                            <Input
                              type="text"
                              placeholder="PAN No."
                              name="panNo"
                              required
                              value={form.panNo}
                              onChange={handleInputChange}
                            />
                          </Col>
                        </Row>
                        <Row>
                          <Col md="6" className="mb-2">
                            <label htmlFor="address">Address</label>
                            <Input
                              type="text"
                              placeholder="Address"
                              name="address"
                              value={form.address}
                              onChange={handleInputChange}
                            />
                          </Col>
                          <Col md="6" className="mb-2">
                            <label htmlFor="state">State</label>
                            <Input
                              type="select"
                              name="state"
                              value={form.state}
                              onChange={handleInputChange}
                            >
                              <option value="0">Select State</option>
                              {StateList.map(({ state }, index) => (
                                <option value={state} key={index}>
                                  {state}
                                </option>
                              ))}
                            </Input>
                          </Col>
                        </Row>
                        <Row className="mt-2 mb-0">
                          <Col md="12" className="text-center mt-3">
                            <Button
                              type="submit"
                              className="btn btn-success btn-md"
                            >
                              Register
                            </Button>
                          </Col>
                        </Row>
                      </Form>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <div className="mt-4 text-center">
                <p>
                  Already have an account ?{" "}
                  <Link to="/login" className="fw-medium text-primary">
                    {" "}
                    Login
                  </Link>{" "}
                </p>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  );
};

export default Register;
