import React, { useState, useEffect } from "react";

import "./custom.scss";

import { withRouter } from "react-router";
import MetaTags from "react-meta-tags";
import { Row, Col, Card, CardBody } from "reactstrap";

import Select from "react-select";

import Breadcrumbs from "../../components/Common/Breadcrumb";

import { fetchHelper } from "../../helpers/apiHelper";
import { partyURL } from "../../helpers/urlHelpers";

import { subDays, addDays } from "date-fns";

import { DateRangePicker } from "rsuite";
import "rsuite/dist/rsuite.min.css";
import ProductReport from "./ProductReport";
import GSTReport from "./GSTReport";
import PaymentReport from "./PaymentReport";

const Reports = (props) => {
  const [parties, setParties] = useState([]);
  const [selectedParty, setSelectedParty] = useState({
    label: "All",
    value: 0,
  });

  const [selectedReportType, setSelectedReportType] = useState({
    label: "Product",
    value: "product",
  });

  const [duration, setDuration] = useState({
    startDate: subDays(new Date(), 6),
    endDate: new Date(),
  });

  const calendarRanges = [
    {
      label: "Yesterday",
      value: [
        subDays(new Date(new Date().setHours(0, 0, 0, 0)), 1),
        subDays(new Date(new Date().setHours(23, 59, 59, 999)), 1),
      ],
    },
    {
      label: "Today",
      value: [
        new Date(new Date().setHours(0, 0, 0, 0)),
        new Date(new Date().setHours(23, 59, 59, 999)),
      ],
    },
    {
      label: "Tomorrow",
      value: [
        addDays(new Date(new Date().setHours(0, 0, 0, 0)), 1),
        addDays(new Date(new Date().setHours(23, 59, 59, 999)), 1),
      ],
    },
    {
      label: "Last 7 Days",
      value: [
        subDays(new Date(new Date().setHours(0, 0, 0, 0)), 6),
        new Date(new Date().setHours(23, 59, 59, 999)),
      ],
    },
    {
      label: "Last Month",
      value: [
        subDays(new Date(new Date().setHours(0, 0, 0, 0)), 29),
        new Date(new Date().setHours(23, 59, 59, 999)),
      ],
    },
  ];

  useEffect(async () => {
    await fetchParties();
  }, []);

  const fetchParties = async () => {
    const headers = {
      "Content-Type": "application/json",
    };

    let data = await fetchHelper(partyURL, { headers });
    if (data.status) {
      setParties(data.data);
    } else {
      alert(data.message);
    }
  };

  const render = (party) => {
    const props = { duration, party };
    switch (selectedReportType.value) {
      case "product":
        return <ProductReport {...props} />;
        break;
      case "gst":
        return <GSTReport {...props} />;
        break;
      case "payment":
        return <PaymentReport {...props} />;
        break;
    }
  };

  return (
    <React.Fragment>
      <div className="page-content">
        <MetaTags>
          <title>Dashboard | Reports</title>
        </MetaTags>
        <div className="container-fluid">
          <Breadcrumbs title="Dashboard" breadcrumbItem="reports" />
          <Row>
            <Col className="col-12">
              <Card>
                <CardBody>
                  <React.Fragment>
                    {/* Search Bar & Add Product Button */}
                    <Row className="mb-2">
                      <Col md="4" sm="12" xs="12" className="mb-2">
                        <Row>
                          <Col xs={12} className="text-center">
                            <h5>Select Sales Dates</h5>
                          </Col>
                          <Col xs={12}>
                            <DateRangePicker
                              className="payment-detail-calendar mb-2 date-range-picker"
                              style={{ width: "100%" }}
                              onChange={(item) => {
                                let [startDate, endDate] = item;
                                setDuration({ startDate, endDate });
                              }}
                              showOneCalendar
                              format="dd/MM/yyyy"
                              defaultValue={[
                                duration.startDate,
                                duration.endDate,
                              ]}
                              ranges={calendarRanges}
                            />
                          </Col>
                        </Row>
                      </Col>
                      <Col md="4" sm="6" xs="12" className="mb-2">
                        <Row>
                          <Col xs={12} className="text-center">
                            <h5>Select Report Type</h5>
                          </Col>
                          <Col xs={12}>
                            <Select
                              options={[
                                { label: "Product", value: "product" },
                                { label: "GST", value: "gst" },
                                { label: "Payment", value: "payment" },
                              ]}
                              defaultValue={selectedReportType}
                              onChange={(evt) => {
                                setSelectedReportType(evt);
                              }}
                            />
                          </Col>
                        </Row>
                      </Col>
                      <Col md="4" sm="6" xs="12" className="mb-2">
                        <Row>
                          <Col xs={12} className="text-center">
                            <h5>Select Party</h5>
                          </Col>
                          <Col xs={12}>
                            <Select
                              options={[
                                { label: "All", value: 0 },
                                ...parties.map((party) => ({
                                  label: party.partyName,
                                  value: party._id,
                                })),
                              ]}
                              defaultValue={selectedParty}
                              onChange={(evt) => {
                                setSelectedParty(evt);
                              }}
                            />
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                    <Row>{render(selectedParty.value)}</Row>
                  </React.Fragment>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </React.Fragment>
  );
};

export default withRouter(Reports);
