import React, { useState, useEffect } from "react";
import { withRouter } from "react-router";
import MetaTags from "react-meta-tags";
import { Row, Col } from "reactstrap";

// datatable related plugins
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory, {
  PaginationProvider,
  PaginationListStandalone,
  SizePerPageDropdownStandalone,
} from "react-bootstrap-table2-paginator";

import ToolkitProvider from "react-bootstrap-table2-toolkit";

import { fetchHelper } from "../../../helpers/apiHelper";
import { reportURL } from "../../../helpers/urlHelpers";

import { gstReportColumns } from "./tableColumns";
import { ExportToExcel } from "../../../helpers/ExportToExcel";
import moment from "moment";

function GSTReport({ duration, party }) {
  const [reportList, setReportList] = useState([]);
  const [excelList, setExcelList] = useState([]);
  const [sizePerPage, setSizePerPage] = useState(10);

  useEffect(async () => {
    await fetchGSTReportList();
  }, [duration, party]);

  const fetchGSTReportList = async () => {
    const headers = {
      "Content-Type": "application/json",
    };

    const body = JSON.stringify({ duration, party });

    let data = await fetchHelper(`${reportURL}/gst`, {
      headers,
      body,
      method: "POST",
    });

    if (data.status) {
      setReportList(data.data);
      setExcelList([
        ...data.data.map((report) => ({
          "Sr. No.": report.srNo,
          Party: report.party.partyName,
          "Inv. No.": report.salesNo,
          Date: moment(report.salesDate).format("DD MMM, YYYY"),
          CGST: report.cgstAmt,
          SGST: report.sgstAmt,
          IGST: report.igstAmt,
          "Challan No.": report.challanNo,
        })),
        {},
        {
          "Sr. No.": `Total : ${data.data.length}`,
          Party: "",
          "Inv. No.": "",
          Date: "",
          CGST: data.data
            .map((report) => report.cgstAmt)
            .reduce((prev, curr) => prev + curr, 0),
          SGST: data.data
            .map((report) => report.sgstAmt)
            .reduce((prev, curr) => prev + curr, 0),
          IGST: data.data
            .map((report) => report.igstAmt)
            .reduce((prev, curr) => prev + curr, 0),
          "Challan No.": "",
        },
      ]);
    } else {
      alert(data.message);
    }
  };

  const defaultSorted = [
    {
      dataField: "srNo",
      order: "asc",
    },
  ];

  const sizePerPageList = [
    { text: "10", value: 10 },
    { text: "15", value: 15 },
    { text: "20", value: 20 },
    { text: "All", value: reportList.length },
  ];

  const pageOptions = {
    sizePerPage,
    totalSize: reportList.length,
    sizePerPageList,
    custom: true,
  };

  return (
    <React.Fragment>
      <MetaTags>
        <title>Dashboard | GST Report</title>
      </MetaTags>
      <div className="container-fluid">
        <PaginationProvider
          pagination={paginationFactory(pageOptions)}
          keyField="_id"
          columns={gstReportColumns}
          data={reportList}
        >
          {({ paginationProps, paginationTableProps }) => (
            <ToolkitProvider
              keyField="_id"
              columns={gstReportColumns}
              data={reportList}
              search
            >
              {(toolkitProps) => (
                <React.Fragment>
                  {/* Export Excel Button */}
                  <Row>
                    <Col xs="12" className="float-end">
                      <ExportToExcel
                        data={excelList}
                        fileName="GST Report"
                        btnText="Export To Excel"
                      />
                    </Col>
                  </Row>

                  {/* Table Row */}
                  <Row>
                    <Col xl="12">
                      <div className="table-responsive">
                        <BootstrapTable
                          keyField={"_id"}
                          responsive
                          bordered={false}
                          striped={false}
                          defaultSorted={defaultSorted}
                          classes={"table align-middle table-nowrap"}
                          headerWrapperClasses={"thead-light"}
                          {...toolkitProps.baseProps}
                          {...paginationTableProps}
                        />
                      </div>
                    </Col>
                  </Row>

                  {/* Pagination Row, i.e., Size per page option & Page number option */}
                  <Row className="align-items-md-center mt-3">
                    <Col className="inner-custom-pagination d-flex">
                      <div className="d-inline">
                        <SizePerPageDropdownStandalone {...paginationProps} />
                      </div>
                      <div className="text-md-right ms-auto">
                        <PaginationListStandalone {...paginationProps} />
                      </div>
                    </Col>
                  </Row>
                </React.Fragment>
              )}
            </ToolkitProvider>
          )}
        </PaginationProvider>
      </div>
    </React.Fragment>
  );
}

export default withRouter(GSTReport);
