import React, { useEffect, useState } from 'react'
import { Container, Row, Col, Card, CardBody, Media } from 'reactstrap'
import { MetaTags } from 'react-meta-tags'
import { Link, withRouter } from 'react-router-dom'

import Breadcrumbs from '../../components/Common/Breadcrumb'
import { fetchHelper } from '../../helpers/apiHelper'
import {
  getToken,
  getUserId,
  resetSessionItems,
} from '../../helpers/authHelper'
import { dashboardURL } from '../../helpers/urlHelpers'
import './dashboard.scss'

function Dashboard(props) {
  const [partyCnt, setPartyCnt] = useState(0)
  const [salesCnt, setSalesCnt] = useState(0)
  const [productsCnt, setProductsCnt] = useState(0)

  useEffect(async () => {
    await fetchTotalCounts()
  }, [])

  const fetchTotalCounts = async () => {
    try {
      let data = await fetchHelper(dashboardURL)
      if (data.status) {
        let { party, sales, product } = data.data
        setPartyCnt(party)
        setProductsCnt(product)
        setSalesCnt(sales)
      } else {
        alert(data.message)
      }
    } catch (err) {
      console.error(err)
      alert(err.message)
    }
  }

  return (
    <React.Fragment>
      <div className="page-content dashboard">
        <MetaTags>
          <title>Dashboard | SBH</title>
        </MetaTags>
        <Container fluid>
          <Breadcrumbs title="Dashboard" breadcrumbItem="home" />
          <Row>
            <Col md="4" sm="6" style={{ border: '10px solid red !important' }}>
              <Link
                to={{ pathname: '/party' }}
                style={{
                  textDecoration: 'none',
                  color: 'black',
                  cursor: 'default',
                }}
              >
                <Card className="mini-stats-wid">
                  <CardBody>
                    <Media>
                      <Media body>
                        <p className="text-muted fw-medium">Total Party</p>
                        <h4 className="mb-0">{partyCnt}</h4>
                      </Media>
                      <div className="mini-stat-icon avatar-sm rounded-circle bg-primary align-self-center">
                        <span className="avatar-title">
                          <i className="mdi mdi-human-queue font-size-24" />
                        </span>
                      </div>
                    </Media>
                  </CardBody>
                </Card>
              </Link>
            </Col>
            {/* <Col md="4" sm="6">
              <Link
                to={{ pathname: "/product" }}
                style={{
                  textDecoration: "none",
                  color: "black",
                  cursor: "default",
                }}
              >
                <Card className="mini-stats-wid">
                  <CardBody>
                    <Media>
                      <Media body>
                        <p className="text-muted fw-medium">Total Products</p>
                        <h4 className="mb-0">{productsCnt}</h4>
                      </Media>
                      <div className="mini-stat-icon avatar-sm rounded-circle bg-primary align-self-center">
                        <span className="avatar-title">
                          <i className={"far fa-object-group font-size-24"} />
                        </span>
                      </div>
                    </Media>
                  </CardBody>
                </Card>
              </Link>
            </Col> */}
            <Col md="4" sm="6">
              <Link
                to={{ pathname: '/sales' }}
                style={{
                  textDecoration: 'none',
                  color: 'black',
                  cursor: 'default',
                }}
              >
                <Card className="mini-stats-wid">
                  <CardBody>
                    <Media>
                      <Media body>
                        <p className="text-muted fw-medium">Total Sales</p>
                        <h4 className="mb-0">{salesCnt}</h4>
                      </Media>
                      <div className="mini-stat-icon avatar-sm rounded-circle bg-primary align-self-center">
                        <span className="avatar-title">
                          <i className={'mdi mdi-point-of-sale font-size-24'} />
                        </span>
                      </div>
                    </Media>
                  </CardBody>
                </Card>
              </Link>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  )
}

export default withRouter(Dashboard)
