import React, { useState, useEffect } from "react";
import MetaTags from "react-meta-tags";
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
} from "reactstrap";

import "./datatables.scss";

import { Plus, Minus } from "react-feather";

import { Table, Thead, Tr, Th, Tbody, Td } from "react-super-responsive-table";

import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import { fetchHelper } from "../../helpers/apiHelper";
import { partyURL, salesURL } from "../../helpers/urlHelpers";

import CreatableSelect from "react-select/creatable";

const SalesAdd = (props) => {
  const emptySalesDetails = {
    hidden: false,
    product: null,
    qty: null,
    rate: null,
    amt: null,
  };

  const [form, setForm] = useState({
    disPer: 0,
    disAmt: 0,
    cgstPer: 2.5,
    cgstAmt: 0,
    sgstPer: 2.5,
    sgstAmt: 0,
    igstPer: 0,
    igstAmt: 0,
    netAmt: 0,
    roundAmt: 0,
  });

  const [salesNo, setSalesNo] = useState(null);
  const [parties, setParties] = useState([]);
  const [partyOptions, setPartyOptions] = useState([]);
  const [productOptions, setProductOptions] = useState([]);
  const [saleDetails, setSaleDetails] = useState([emptySalesDetails]);

  useEffect(() => {
    fetchNewSalesNo();
    fetchPartiesList();
  }, []);

  const fetchNewSalesNo = async () => {
    try {
      let data = await fetchHelper(`${salesURL}/getNewSalesNo`);
      if (data.status) {
        setSalesNo(data.data);
      } else {
        alert(data.message);
      }
    } catch (err) {
      console.error(err);
      alert(err.message);
    }
  };

  const fetchPartiesList = async () => {
    try {
      let data = await fetchHelper(`${partyURL}`);
      if (data.status) {
        setParties(data.data);
        setPartyOptions(
          data.data.map((party) => ({
            label: party.partyName,
            value: party._id,
          }))
        );
      } else {
        alert(data.message);
      }
    } catch (err) {
      console.error(err);
      alert(err.message);
    }
  };

  const fetchProductsList = async (party) => {
    if (party == 0) {
      setProductOptions([]);
      return;
    }
    try {
      const options = {
        headers: {
          "Content-Type": "application/json",
        },
        method: "POST",
        body: JSON.stringify({ party }),
      };
      let data = await fetchHelper(`${salesURL}/getPartyWiseProducts`, options);
      if (data.status) {
        // setProducts(data.data);
        setProductOptions(
          data.data.map((product) => ({
            label: product,
            value: product,
          }))
        );
      } else {
        alert(data.message);
      }
    } catch (err) {
      console.error(err);
      alert(err.message);
    }
  };

  const handleInputChange = async (evt) => {
    setForm({ ...form, [evt.target.name]: evt.target.value });
  };

  const handleValidSubmit = async (evt) => {
    evt.preventDefault();
    try {
      if (
        saleDetails.filter(
          (details) =>
            !details.hidden && !(details.product && details.qty && details.rate)
        ).length
      ) {
        return alert("Enter all required fields");
      }

      let options = {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          sales: {
            ...form,
            salesDate: form.salesDate || new Date(Date.now()),
            salesNo,
          },
          saleDetails: saleDetails.filter((data) => !data.hidden),
        }),
      };
      let data = await fetchHelper(`${salesURL}/createSales`, options);
      alert(data.message);
      if (data.status) {
        props.history.push("/sales");
      } else {
        alert(data.message);
      }
    } catch (err) {
      console.error(err);
      alert(err.message);
    }
  };

  const handleSaleDetailsArray = async (idx, field, value) => {
    setSaleDetails([
      ...saleDetails.map((detail, index) => {
        if (idx == index) {
          detail[field] = value;
        }
        return detail;
      }),
    ]);
  };

  const handleProductQtyChange = (evt, index) => {
    let qty = evt.target.value;
    let rate = saleDetails[index].rate;
    let amt = (qty * (rate || 0)).toFixed(2);

    handleSaleDetailsArray(index, "qty", qty);
    handleSaleDetailsArray(index, "amt", amt);

    updateFormConstants(saleDetails);
  };

  const handleProductRateChange = (evt, index) => {
    let rate = evt.target.value;
    let qty = saleDetails[index].qty;
    let amt = (rate * (qty || 0)).toFixed(2);

    handleSaleDetailsArray(index, "rate", rate);
    handleSaleDetailsArray(index, "amt", amt);

    updateFormConstants(saleDetails);
  };

  const updateFormConstants = (saleDetails = []) => {
    form.totalQty = saleDetails.reduce((prev, curr) => ({
      qty: Number.parseFloat(prev.qty || 0) + Number.parseFloat(curr.qty || 0),
    })).qty;

    form.totalAmt = saleDetails.reduce((prev, curr) => ({
      amt: Number.parseFloat(prev.amt || 0) + Number.parseFloat(curr.amt || 0),
    })).amt;

    calculateTotals();
  };

  const handleChange = (newValue, actionMeta, idx) => {
    if (!form.party) return;

    if (actionMeta.action == "create-option") {
      productOptions.push({ label: newValue.label, value: newValue.value });
    }

    setSaleDetails([
      ...saleDetails.map((detail, index) => {
        if (idx == index) {
          detail["product"] = newValue?.value;
        }
        return detail;
      }),
    ]);
  };

  const addDetailsRow = (idx) => (
    <tr
      className="add-sale-details"
      id={"addr" + idx}
      key={idx}
      hidden={saleDetails[idx].hidden}
    >
      <td>
        <CreatableSelect
          isClearable
          onChange={(newValue, actionMeta) => {
            handleChange(newValue, actionMeta, idx);
          }}
          options={productOptions}
          placeholder="Select Product"
        />
      </td>
      <td>
        <Input
          type="number"
          step="any"
          min="0"
          className="form-control"
          name="qty"
          required={!saleDetails[idx].hidden}
          value={saleDetails[idx].qty}
          placeholder="Quantity"
          onChange={(evt) => handleProductQtyChange(evt, idx)}
        />
      </td>
      <td>
        <Input
          type="number"
          step="any"
          min="0"
          className="form-control"
          name="rate"
          required={!saleDetails[idx].hidden}
          value={saleDetails[idx].rate}
          placeholder="Rate"
          onChange={(evt) => handleProductRateChange(evt, idx)}
        />
      </td>
      <td>
        <Input
          className="form-control"
          style={{ textAlign: "right" }}
          name="amt"
          value={saleDetails[idx].amt || 0}
          readOnly
        />
      </td>
      <td>
        <Button
          name="btn-action"
          onClick={() =>
            idx == saleDetails.length - 1
              ? handleAddRow()
              : handleRemoveRow(idx)
          }
          className="btn btn-sm"
          color={idx == saleDetails.length - 1 ? "success" : "danger"}
        >
          {idx == saleDetails.length - 1 ? (
            <Plus size={15} />
          ) : (
            <Minus size={15} />
          )}
        </Button>
      </td>
    </tr>
  );

  const handleAddRow = () => {
    setSaleDetails([...saleDetails, emptySalesDetails]);
  };

  const handleRemoveRow = (idx) => {
    let temp = saleDetails.map((data, index) => {
      if (idx == index) {
        data.hidden = true;
        data.product = null;
        data.qty = null;
        data.rate = null;
        data.amt = null;
      }
      return data;
    });
    setSaleDetails(temp);
    updateFormConstants(temp);
  };

  const calculateTotals = () => {
    let totalAmt, disAmt, cgstAmt, sgstAmt, igstAmt;

    disAmt = (
      (parseFloat(form.disPer || 0) * parseFloat(form.totalAmt || 0)) /
      100
    ).toFixed(2);

    totalAmt = (
      parseFloat(form.totalAmt || 0) - parseFloat(disAmt || 0)
    ).toFixed(2);

    sgstAmt = ((parseFloat(form.sgstPer || 0) * totalAmt) / 100).toFixed(2);

    cgstAmt = ((parseFloat(form.cgstPer || 0) * totalAmt) / 100).toFixed(2);

    igstAmt = ((parseFloat(form.igstPer || 0) * totalAmt) / 100).toFixed(2);

    let netAmt = (
      parseFloat(totalAmt) +
      parseFloat(cgstAmt) +
      parseFloat(sgstAmt) +
      parseFloat(igstAmt)
    ).toFixed(2);

    setForm({
      ...form,
      disAmt,
      sgstAmt,
      cgstAmt,
      igstAmt,
      netAmt,
      roundAmt: Math.ceil(netAmt),
    });
  };

  return (
    <React.Fragment>
      <div className="page-content" style={{ color: "black" }}>
        <MetaTags>
          <title>Dashboard | Sales Add</title>
        </MetaTags>
        <Container fluid={true} className="p-0">
          {/* <Breadcrumbs title="Dashboard" breadcrumbItem="sales-add" /> */}
          <Row>
            <Col lg={12}>
              <Card className="m-0">
                <CardBody>
                  <CardTitle className="h4 mb-3">Add Sales Details</CardTitle>

                  <Form autoComplete="off" onSubmit={handleValidSubmit}>
                    <FormGroup className="row">
                      <Col xs={12} sm={6} md={3} className="mb-1">
                        <Row>
                          <Col mg={6}>
                            <div className="form-floating mb-1">
                              <Input
                                type="text"
                                className="form-control"
                                name="salesNo"
                                readOnly
                                value={salesNo}
                              />
                              <Label htmlFor="salesNo">Sales Number</Label>
                            </div>
                          </Col>
                        </Row>
                      </Col>
                      <Col xs={12} sm={6} md={3} className="mb-1">
                        <Row>
                          <Col mg={6}>
                            <div className="form-floating mb-1">
                              <Input
                                type="select"
                                className="form-select"
                                name="party"
                                onChange={(evt) => {
                                  let disPer =
                                    evt.target.value == "0"
                                      ? 0
                                      : parties.filter(
                                          (party) =>
                                            party._id == evt.target.value
                                        )[0].discount;
                                  setForm({
                                    ...form,
                                    party: evt.target.value,
                                    disPer,
                                  });
                                  fetchProductsList(evt.target.value);
                                }}
                              >
                                <option defaultValue value="0">
                                  Select Party
                                </option>
                                {partyOptions.map((party, index) => (
                                  <option key={index} value={party.value}>
                                    {party.label}
                                  </option>
                                ))}
                              </Input>
                              <Label htmlFor="party">Party</Label>
                            </div>
                          </Col>
                        </Row>
                      </Col>
                      <Col xs={12} sm={6} md={3} className="mb-1">
                        <Row>
                          <Col mg={6}>
                            <div className="form-floating mb-1">
                              <Flatpickr
                                name="salesDate"
                                className="form-control d-block"
                                placeholder="Pick Sales Date"
                                options={{
                                  altInput: true,
                                  dateFormat: "d-m-y",
                                }}
                                defaultValue={new Date(
                                  Date.now()
                                ).toUTCString()}
                                onChange={(date) => {
                                  setForm({ ...form, salesDate: date[0] });
                                }}
                              />
                              <Label htmlFor="salesDate">Date</Label>
                            </div>
                          </Col>
                          {/* <Label
                            htmlFor="salesDate"
                            className="col-sm-3 col-md-3 col-form-label"
                          >
                            Date
                          </Label>
                          <Col sm={9}>
                            <Flatpickr
                              name="salesDate"
                              className="form-control d-block"
                              placeholder="Pick Sales Date"
                              options={{
                                altInput: true,
                                dateFormat: "d-m-y",
                              }}
                              defaultValue={new Date(Date.now()).toUTCString()}
                              onChange={(date) => {
                                setForm({ ...form, salesDate: date[0] });
                              }}
                            />
                          </Col> */}
                        </Row>
                      </Col>
                      <Col xs={12} sm={6} md={3} className="mb-1">
                        <Row>
                          <Col mg={6}>
                            <div className="form-floating mb-1">
                              <Input
                                type="text"
                                className="form-control"
                                name="challanNo"
                                onChange={handleInputChange}
                                required
                              />
                              <Label htmlFor="challanNo">Challan No</Label>
                            </div>
                          </Col>
                        </Row>
                      </Col>
                    </FormGroup>

                    <FormGroup className="mt-1 mb-3">
                      <div className="order-details-table-div">
                        <table
                          className="order-details-table-main"
                          style={{
                            textAlign: "center",
                            width: "100%",
                          }}
                        >
                          <tbody>
                            <tr className="">
                              {/* row */}

                              <td className="col-sm-5 col-5">
                                {/* col-sm-5 */}
                                <Label tag="h6">Product</Label>
                              </td>

                              <td className="col-sm-2 col-2">
                                {/* col-sm-2 */}
                                <Label tag="h6">Quantity</Label>
                              </td>

                              <td className="col-sm-2 col-2">
                                {/* col-sm-2 */}
                                <Label tag="h6">Rate</Label>
                              </td>

                              <td className="col-sm-2 col-2">
                                {/* col-sm-2 */}
                                <Label tag="h6">Amount</Label>
                              </td>

                              <td className="col-sm-1 col-1">
                                {/* col-sm-1 */}
                              </td>
                            </tr>
                            {saleDetails.map((sale, index) => (
                              <React.Fragment key={index}>
                                {addDetailsRow(index)}
                              </React.Fragment>
                            ))}
                          </tbody>
                        </table>
                      </div>
                    </FormGroup>

                    <FormGroup>
                      <Row
                        className="d-flex"
                        style={{ flexDirection: "row-reverse" }}
                      >
                        <Col sm={6} xs={12}>
                          <div className="form-floating mb-1">
                            <Input
                              name="totalQty"
                              type="number"
                              step="any"
                              className="form-control"
                              readOnly
                              value={form.totalQty || 0}
                              onChange={(evt) => calculateTotals()}
                            />
                            <Label htmlFor="totalQty">Total Quantity</Label>
                          </div>

                          <div className="form-floating mb-1">
                            <Input
                              name="totalAmt"
                              type="number"
                              step="any"
                              className="form-control"
                              readOnly
                              value={form.totalAmt || 0}
                              onChange={(evt) => calculateTotals()}
                            />
                            <Label htmlFor="totalAmt">
                              Amount Before Discount
                            </Label>
                          </div>

                          <div className="form-floating mb-1">
                            <Input
                              name="disPer"
                              type="number"
                              step="any"
                              min="0"
                              className="form-control"
                              value={form.disPer}
                              onChange={(evt) => {
                                form.disPer = evt.target.value;
                                calculateTotals();
                              }}
                            />
                            <Label htmlFor="disPer">Discount (%)</Label>
                          </div>

                          <div className="form-floating mb-1">
                            <Input
                              name="disAmt"
                              type="text"
                              className="form-control"
                              value={form.disAmt}
                              readOnly
                            />
                            <Label htmlFor="disAmt">Discount Amount</Label>
                          </div>

                          <div className="form-floating mb-1">
                            <Input
                              name="taxableAmt"
                              type="text"
                              readOnly
                              className="form-control"
                              value={
                                parseFloat(form.totalAmt || 0) -
                                parseFloat(form.disAmt || 0)
                              }
                            />
                            <Label htmlFor="taxableAmt">Taxable Amount</Label>
                          </div>

                          <div className="form-floating mb-1">
                            <Input
                              name="gstPer"
                              type="number"
                              step="any"
                              min="0"
                              className="form-control"
                              value={
                                parseFloat(form.cgstPer || 0) +
                                  parseFloat(form.sgstPer || 0) +
                                  parseFloat(form.igstPer || 0) || ""
                              }
                              onChange={(evt) => {
                                let per = parseFloat(evt.target.value || 0);
                                form.cgstPer = per / 2;
                                form.sgstPer = per / 2;
                                calculateTotals();
                              }}
                            />
                            <Label htmlFor="gstPer">GST (%)</Label>
                          </div>

                          <div className="form-floating mb-1">
                            <Input
                              name="taxableAmt"
                              type="text"
                              readOnly
                              className="form-control"
                              value={
                                parseFloat(form.cgstAmt || 0) +
                                parseFloat(form.sgstAmt || 0) +
                                parseFloat(form.igstAmt || 0)
                              }
                            />
                            <Label htmlFor="taxableAmt">GST Amount</Label>
                          </div>

                          <div className="form-floating mb-1">
                            <Input
                              name="netAmt"
                              type="text"
                              readOnly
                              className="form-control"
                              value={form.netAmt}
                            />
                            <Label htmlFor="netAmt">Net Amount</Label>
                          </div>

                          <div className="form-floating mb-1">
                            <Input
                              name="roundAmt"
                              type="number"
                              step="any"
                              className="form-control"
                              value={form.roundAmt}
                              onChange={(evt) =>
                                setForm({ ...form, roundAmt: evt.target.value })
                              }
                            />
                            <Label htmlFor="netAmt">Round Amount</Label>
                          </div>
                        </Col>

                        <Col sm={6} xs={12}>
                          <div className="form-floating mb-3">
                            <Input
                              type="textarea"
                              className="form-control"
                              name="remarks"
                              value={form.remarks}
                              onChange={(evt) =>
                                setForm({ ...form, remarks: evt.target.value })
                              }
                              style={{ height: "150px" }}
                            />
                            <Label htmlFor="remarks">Remarks</Label>
                          </div>
                        </Col>
                      </Row>
                    </FormGroup>

                    <FormGroup className="row text-center mt-4">
                      <Col xs="6" className="text-end">
                        <Button type="submit" color="primary" className="w-xxs">
                          Save
                        </Button>
                      </Col>
                      <Col xs="6" className="text-start">
                        <Button
                          type="button"
                          color="danger"
                          className="w-xxs"
                          onClick={() => props.history.push("/sales")}
                        >
                          Cancel
                        </Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  );
};

export default SalesAdd;
