export const fetchHelper = async (url, config = {}) => {
  let options = {
    ...config,
    headers: {
      ...config.headers,
      authorization: `Bearer ${getToken()}`,
    },
  };
  return await fetch(url, { ...options }).then((res) => res.json());
};
