import React, { useState, useEffect } from "react";
import { withRouter } from "react-router";
import MetaTags from "react-meta-tags";
import {
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Button,
  Modal,
  ModalBody,
  Input,
  Form,
  ModalHeader,
} from "reactstrap";

import { Edit, Trash2, BookOpen } from "react-feather";

// datatable related plugins
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory, {
  PaginationProvider,
  PaginationListStandalone,
  SizePerPageDropdownStandalone,
} from "react-bootstrap-table2-paginator";

import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import "./datatables.scss";

import { fetchHelper } from "../../helpers/apiHelper";
import { partyURL } from "../../helpers/urlHelpers";
import { CityList } from "../../common/CityList";
import { StateList } from "../../common/StateList";
import { Link } from "react-router-dom";

function Party(props) {
  const [party, setParty] = useState({});
  const [parties, setParties] = useState([]);
  const [page, setPage] = useState(1);
  const [sizePerPage, setSizePerPage] = useState(10);
  const [modalOpen, setOpenModal] = useState(false);
  const [form, setForm] = useState();
  const [updateForm, setUpdateForm] = useState();
  const [errMsg, setErrMsg] = useState(null);
  const [isEdit, setIsEdit] = useState(false);

  useEffect(async () => {
    await fetchParties();
  }, []);

  const fetchParties = async () => {
    const headers = {
      "Content-Type": "application/json",
    };

    let data = await fetchHelper(partyURL, { headers });
    if (data.status) {
      setParties(data.data);
    } else {
      alert(data.message);
    }
  };

  const handleDeleteParty = async (partyId) => {
    let headers = {
      "Content-Type": "application/json",
    };

    let body = JSON.stringify({
      partyId,
    });

    let options = {
      method: "DELETE",
      headers,
      body,
    };

    let data = await fetchHelper(`${partyURL}`, options);
    if (data.status) {
      await fetchParties();
    } else {
    }
    alert(data.message);
  };

  const handleInputChange = (evt) =>
    setForm({ ...form, [evt.target.name]: evt.target.value });

  const validateInputChange = async (evt) => {
    let body = { field: evt.target.name, value: evt.target.value };
    body = JSON.stringify(body);
    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "POST",
      headers,
      body,
    };
    let data = await fetchHelper(partyURL + "/validate", options);
    setErrMsg(data.status ? null : data.message);
  };

  const handleFormSubmit = async (evt) => {
    evt.preventDefault();
    if (errMsg) return;

    let body = JSON.stringify(form);
    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "POST",
      headers,
      body,
    };
    let data = await fetchHelper(partyURL, options);
    alert(data.message);
    if (data.status) {
      setOpenModal(false);
      await fetchParties();
    } else {
      setOpenModal(true);
    }
  };

  const handleUpdateInputChange = (evt) =>
    setUpdateForm({ ...updateForm, [evt.target.name]: evt.target.value });

  const handleUpdateParty = async (evt) => {
    evt.preventDefault();
    if (errMsg) return;

    let body = JSON.stringify(updateForm);
    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "PUT",
      headers,
      body,
    };
    let data = await fetchHelper(`${partyURL}/${party._id}`, options);
    if (data.status) {
      setIsEdit(false);
      setUpdateForm({});
      setParty({});
      await fetchParties();
    } else {
      setIsEdit(true);
      alert(data.message);
    }
  };

  const columns = [
    {
      dataField: "partyName",
      text: "Name",
      sort: true,
    },
    {
      dataField: "mobileNo",
      text: "Mobile No.",
      sort: true,
    },
    {
      dataField: "gstNo",
      text: "GST No.",
      sort: true,
    },
    {
      dataField: "panNo",
      text: "PAN No.",
      sort: true,
    },
    {
      dataField: "city",
      text: "City",
      sort: true,
    },
    {
      dataField: "state",
      text: "State",
      sort: true,
    },
    {
      dataField: "discount",
      text: "Discount %",
      sort: true,
    },
    {
      dataField: "details",
      isDummyField: true,
      text: "Details",
      sort: false,
      formatter: (cellContent, row) => (
        <div>
          <Link
            to={{ pathname: "/partyDetails", state: row }}
            className="btn btn-sm btn-info"
          >
            Show
          </Link>
        </div>
      ),
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      formatter: (cellContent, row) => (
        <div className="d-flex gap-3 text-center">
          <Edit
            size={20}
            onClick={() => {
              setIsEdit(true);
              setParty(row);
            }}
            className="text-primary"
          />
          <Trash2
            size={20}
            onClick={async () => await handleDeleteParty(row._id)}
            className="text-danger"
          />
        </div>
      ),
    },
  ];

  const defaultSorted = [
    {
      dataField: "partyName",
      order: "asc",
    },
  ];

  // Custom Pagination Toggle
  const sizePerPageList = [
    { text: "5", value: 5 },
    { text: "10", value: 10 },
    { text: "15", value: 15 },
    { text: "20", value: 20 },
    { text: "25", value: 25 },
    { text: "All", value: parties.length },
  ];

  const pageOptions = {
    sizePerPage,
    totalSize: parties.length,
    sizePerPageList,
    custom: true,
  };

  // Select All Button operation
  const selectRow = {
    mode: "checkbox",
  };

  const { SearchBar } = Search;

  return (
    <React.Fragment>
      <div className="page-content">
        <MetaTags>
          <title>Dashboard | Party</title>
        </MetaTags>
        <div className="container-fluid">
          <Breadcrumbs title="Dashboard" breadcrumbItem="party" />
          <Row>
            <Col className="col-12">
              <Card>
                <CardBody>
                  {/* <CardTitle className="h4">Party</CardTitle> */}

                  {/* Pagination Provider */}
                  <PaginationProvider
                    pagination={paginationFactory(pageOptions)}
                    keyField="_id"
                    columns={columns}
                    data={parties}
                  >
                    {({ paginationProps, paginationTableProps }) => (
                      <ToolkitProvider
                        keyField="_id"
                        columns={columns}
                        data={parties}
                        search
                      >
                        {(toolkitProps) => (
                          <React.Fragment>
                            {/* Search Bar & Add Party Button */}
                            <Row className="mb-2">
                              <Col sm="6" xs="6">
                                <div className="search-box mb-2 d-inline-block">
                                  <div className="position-relative">
                                    <SearchBar {...toolkitProps.searchProps} />
                                    <i className="bx bx-search-alt search-icon" />
                                  </div>
                                </div>
                              </Col>
                              <Col sm="6" xs="6">
                                <div>
                                  <Button
                                    className="btn btn-success btn-md float-end"
                                    onClick={() => setOpenModal(true)}
                                  >
                                    Add Party
                                  </Button>
                                </div>
                              </Col>
                            </Row>

                            {/* Table Row */}
                            <Row>
                              <Col xl="12">
                                <div className="table-responsive">
                                  <BootstrapTable
                                    keyField={"_id"}
                                    responsive
                                    bordered={false}
                                    striped={false}
                                    defaultSorted={defaultSorted}
                                    // selectRow={selectRow}
                                    classes={"table align-middle table-nowrap"}
                                    headerWrapperClasses={"thead-light"}
                                    {...toolkitProps.baseProps}
                                    {...paginationTableProps}
                                  />
                                </div>
                              </Col>
                            </Row>

                            {/* Pagination Row, i.e., Size per page option & Page number option */}
                            <Row className="align-items-md-center mt-30">
                              <Col className="inner-custom-pagination d-flex">
                                <div className="d-inline">
                                  <SizePerPageDropdownStandalone
                                    {...paginationProps}
                                  />
                                </div>
                                <div className="text-md-right ms-auto">
                                  <PaginationListStandalone
                                    {...paginationProps}
                                  />
                                </div>
                              </Col>
                            </Row>
                          </React.Fragment>
                        )}
                      </ToolkitProvider>
                    )}
                  </PaginationProvider>

                  {/* Modal For Add Party */}
                  <Row>
                    <Modal size="xl" isOpen={modalOpen}>
                      <ModalBody>
                        <ModalHeader
                          toggle={() => {
                            setOpenModal(!modalOpen);
                            setErrMsg(null);
                          }}
                          tag="h4"
                          className="mb-0"
                        >
                          {"Add Party"}
                        </ModalHeader>
                        <ModalHeader
                          style={{ color: "red" }}
                          tag="span"
                          className="mb-0"
                        >
                          {errMsg}
                        </ModalHeader>
                        <Card>
                          <CardBody>
                            <Form
                              onSubmit={handleFormSubmit}
                              autoComplete="off"
                            >
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="partyName">
                                    Name <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="Name"
                                    name="partyName"
                                    required
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="mobileNo">
                                    Mobile No.{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="tel"
                                    placeholder="XXXXXXXXXX"
                                    name="mobileNo"
                                    pattern="[0-9]{10}"
                                    required
                                    onChange={handleInputChange}
                                    onBlur={validateInputChange}
                                    maxLength={10}
                                  />
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="discount">
                                    Discount (%){" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="number"
                                    placeholder="Discount (%)"
                                    name="discount"
                                    required
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="gstNo">
                                    GST No.{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="GST No."
                                    name="gstNo"
                                    required
                                    onChange={handleInputChange}
                                    onBlur={validateInputChange}
                                  />
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="panNo">
                                    PAN No.{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="PAN No."
                                    name="panNo"
                                    required
                                    onChange={handleInputChange}
                                    onBlur={validateInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="address">Address</label>
                                  <Input
                                    type="text"
                                    placeholder="Address"
                                    name="address"
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="state">State</label>
                                  <Input
                                    type="select"
                                    name="state"
                                    onChange={handleInputChange}
                                  >
                                    <option value="0">Select State</option>
                                    {StateList.map((state, index) => (
                                      <option value={state.state} key={index}>
                                        {state.state}
                                      </option>
                                    ))}
                                  </Input>
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="city">City</label>
                                  <Input
                                    type="select"
                                    name="city"
                                    onChange={handleInputChange}
                                  >
                                    <option value="0">Select City</option>
                                    {CityList[form ? form.state || 0 : 0].map(
                                      (city, index) => (
                                        <option value={city} key={index}>
                                          {city}
                                        </option>
                                      )
                                    )}
                                  </Input>
                                </Col>
                              </Row>
                              <Row className="mt-2 mb-0">
                                <Col md="12">
                                  <Button
                                    type="submit"
                                    className="btn btn-success btn-md float-end"
                                  >
                                    Save
                                  </Button>
                                </Col>
                              </Row>
                            </Form>
                          </CardBody>
                        </Card>
                      </ModalBody>
                    </Modal>
                  </Row>

                  {/* Modal For Edit Party */}
                  <Row>
                    <Modal size="xl" isOpen={isEdit}>
                      <ModalBody>
                        <ModalHeader
                          toggle={() => {
                            setIsEdit(!isEdit);
                            setErrMsg(null);
                          }}
                          tag="h4"
                          className="mb-0"
                        >
                          {"Edit Party"}
                        </ModalHeader>
                        <ModalHeader
                          style={{ color: "red" }}
                          tag="span"
                          className="mb-0"
                        >
                          {errMsg}
                        </ModalHeader>
                        <Card>
                          <CardBody>
                            <Form
                              onSubmit={handleUpdateParty}
                              autoComplete="off"
                            >
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="partyName">
                                    Name <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="Name"
                                    name="partyName"
                                    required
                                    defaultValue={party.partyName}
                                    onChange={handleUpdateInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="mobileNo">
                                    Mobile No.{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="tel"
                                    placeholder="XXXXXXXXXX"
                                    name="mobileNo"
                                    pattern="[0-9]{10}"
                                    required
                                    defaultValue={party.mobileNo}
                                    onChange={handleUpdateInputChange}
                                    // onBlur={validateInputChange}
                                  />
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="discount">
                                    Discount (%){" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="number"
                                    placeholder="Discount (%)"
                                    name="discount"
                                    min={0}
                                    required
                                    defaultValue={party.discount}
                                    onChange={handleUpdateInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="gstNo">
                                    GST No.{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="GST No."
                                    name="gstNo"
                                    required
                                    defaultValue={party.gstNo}
                                    onChange={handleUpdateInputChange}
                                    // onBlur={validateInputChange}
                                  />
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="panNo">
                                    PAN No.{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="PAN No."
                                    name="panNo"
                                    required
                                    defaultValue={party.panNo}
                                    onChange={handleUpdateInputChange}
                                    // onBlur={validateInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="address">Address</label>
                                  <Input
                                    type="text"
                                    placeholder="Address"
                                    name="address"
                                    defaultValue={party.address}
                                    onChange={handleUpdateInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="state">State</label>
                                  <Input
                                    type="select"
                                    name="state"
                                    defaultValue={party.state}
                                    onChange={handleUpdateInputChange}
                                  >
                                    <option value="0">Select State</option>
                                    {StateList.map(({ state }, index) => (
                                      <option value={state} key={index}>
                                        {state}
                                      </option>
                                    ))}
                                  </Input>
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="city">City</label>
                                  <Input
                                    type="select"
                                    name="city"
                                    defaultValue={party.city}
                                    onChange={handleUpdateInputChange}
                                  >
                                    <option value="0">Select City</option>
                                    {CityList[
                                      updateForm ? updateForm.state || 0 : 0
                                    ].map((city, index) => (
                                      <option value={city} key={index}>
                                        {city}
                                      </option>
                                    ))}
                                  </Input>
                                </Col>
                              </Row>
                              <Row className="mt-2 mb-0">
                                <Col md="12">
                                  <Button
                                    type="submit"
                                    className="btn btn-info btn-md float-end"
                                  >
                                    Update
                                  </Button>
                                </Col>
                              </Row>
                            </Form>
                          </CardBody>
                        </Card>
                      </ModalBody>
                    </Modal>
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </React.Fragment>
  );
}

export default withRouter(Party);
